
import { defineComponent } from 'vue';
import './style.css';

export default defineComponent({
    name: 'FifthQuestionPage',
    methods: {
        handleClick() {
            this.$store.commit('setCurrentQuestion', 6);
        }
    },
    computed: {
        getUserAge(): number {
            if(this.$store.getters.getUserAge !== null) {
                return this.$store.getters.getUserAge
            } else return 0;
        }
    }
})
